import React, { useLayoutEffect } from "react";
import { DropdownBox } from "../dropdown";
import { List } from "../list";
import { SelectOption } from "../select/SelectOption";
import { StyledProps } from "../_type";

interface TagSelectBoxProps extends StyledProps {
  listRef: React.Ref<HTMLUListElement>;
  options: SelectOption[];
  width: number;
  onChange: (
    value: string,
    context: { event: React.MouseEvent; option: SelectOption }
  ) => void;
  scheduleUpdate: () => void;
  tips: React.ReactNode;
  currentIndex: number;
}

export function TagSelectBox({
  listRef,
  options,
  onChange,
  width,
  scheduleUpdate,
  className,
  style,
  tips,
  currentIndex,
}: TagSelectBoxProps) {
  useLayoutEffect(() => {
    scheduleUpdate();
  }, [options, scheduleUpdate]);

  if (!tips && options.length === 0) {
    return null;
  }

  return (
    <DropdownBox
      onClick={e => e.stopPropagation()}
      className={className}
      style={style}
    >
      <List type="option" style={{ width }} ref={listRef}>
        {tips ? <List.StatusTip>{tips}</List.StatusTip> : null}
        {options.map(({ value, text, ...itemProps }, index) => (
          <List.Item
            key={value}
            current={index === currentIndex}
            onClick={event =>
              onChange(value, { event, option: { value, text, ...itemProps } })
            }
            {...itemProps}
          >
            {text || value}
          </List.Item>
        ))}
      </List>
    </DropdownBox>
  );
}
