import React, { Fragment, useRef, useState, useEffect } from "react";
import classNames from "classnames";
import { Combine, StyledProps } from "../_type";
import {
  Dropdown,
  DropdownProps,
  CommonDropdownProps,
  DropdownKeyDownContext,
} from "../dropdown";
import { List } from "../list";
import { useTranslation } from "../i18n";
import { ControlledProps, useDefaultValue } from "../form";
import { SelectOptionWithGroup } from "./SelectOption";
import { SelectMultiple } from "./SelectMultiple";
import { SizeType } from "../input";
import { SearchBox } from "../searchbox";
import { EmptyTip } from "../tips";
import { useConfig } from "../_util/config-context";
import { SelectSearchProps } from "./SelectProps";
import { KeyCode } from "../_util/key-code";
import { useListScroll } from "../_util/use-list-scroll";

const noop = () => {};

export interface SelectProps
  extends Combine<
    SelectSearchProps<SelectOptionWithGroup>,
    CommonDropdownProps,
    StyledProps,
    ControlledProps<string>
  > {
  /**
   * 下拉选框类型，默认为 `native`，使用原生下拉框
   *
   * 设置为 `simulate` 则使用模拟下拉框
   *
   * @default "native"
   */
  type?: "native" | "simulate";

  /**
   * 下拉选项列表
   */
  options?: SelectOptionWithGroup[];

  /**
   * 分组
   */
  groups?: {
    [groupKey: string]: React.ReactNode;
  };

  /**
   * 占位符
   * @default "请选择"（已处理国际化）
   */
  placeholder?: string;

  /**
   * 展开时回调，仅对 `type = "simulate"` 有效
   */
  onOpen?: DropdownProps["onOpen"];

  /**
   * 收起时回调，仅对 `type = "simulate"` 有效
   */
  onClose?: DropdownProps["onClose"];

  /**
   * 下拉选框的外观，仅对 `type = "simulate"` 有效
   *
   * - `default` 无边框，适用于页面标题和表格内
   * - `button` 为按钮风格，有边框，多用于操作栏中
   * - `link` 为超链接风格
   * - `filter` 为过滤组件风格，多用于表头筛选
   * - `pure` 无额外样式
   *
   * 原有 `raw` 类型建议使用 `pure` 进行改造
   *
   * @default "default"
   * @version 2.0.9
   */
  appearance?: DropdownProps["appearance"];

  /**
   * **\[Deprecated\]** 请使用 `appearance` 属性
   * @deprecated
   */
  appearence?: DropdownProps["appearence"];

  /**
   * 是否禁用下拉选择
   * @default false
   */
  disabled?: boolean;

  /**
   * 下拉按钮的内容，只对 `type = "simulate"` 有效，如果不传，默认会使用选中的选项的内容
   */
  button?: DropdownProps["button"];

  /**
   * 下拉按钮尺寸，使用 `"full"` 撑满容器宽度
   */
  size?: SizeType | "auto";

  /**
   * `options` 滚动至底部的回调，只对 `type = "simulate"` 有效
   */
  onScrollBottom?: (event: React.UIEvent) => void;

  /**
   * 弹出区域尺寸（宽度）是否同步按钮尺寸，仅对 `type = "simulate"` 有效
   *
   * @default false
   */
  boxSizeSync?: boolean;

  /**
   * 弹出区域自定义类名，仅对 `type = "simulate"` 有效
   */
  boxClassName?: DropdownProps["boxClassName"];

  /**
   * 弹出区域自定义样式，仅对 `type = "simulate"` 有效
   */
  boxStyle?: DropdownProps["boxStyle"];

  /**
   * 列表顶部状态提示，仅对 `type = "simulate"` 有效
   *
   * 可使用字符串或 [StatusTip](/component/tips) 相关组件
   */
  tips?: React.ReactNode;

  /**
   * 列表底部状态提示，仅对 `type = "simulate"` 有效
   *
   * 可使用字符串或 [StatusTip](/component/tips) 相关组件
   * @version 2.0.9
   */
  bottomTips?: React.ReactNode;

  /**
   * 支持搜索，仅对 `type = "simulate"` 有效
   * @default false
   */
  searchable?: boolean;

  /**
   * 是否在选中项后或下拉框收起后清空搜索框
   * @default true
   * @version 2.0.9
   */
  autoClearSearchValue?: boolean;
}

export function Select({ type, ...props }: SelectProps) {
  if (type === "simulate") {
    return <SimulateSelect {...props} />;
  }
  return <NativeSelect {...props} />;
}

function groupBy(options: SelectOptionWithGroup[]): SelectOptionWithGroup[][] {
  const groups = [];
  options.forEach((opt, index) => {
    const { groupKey } = opt;
    if (index === 0 || groupKey !== options[index - 1].groupKey) {
      groups.push([]);
    }
    groups[groups.length - 1].push(opt);
  });
  return groups;
}

/**
 * 模拟 Select
 */
function SimulateSelect(props: SelectProps) {
  const t = useTranslation();
  const {
    value,
    onChange,
    options = [],
    groups = {},
    placeholder = t.pleaseSelect,
    button,
    size,
    bottomTips,
    onScrollBottom,
    boxSizeSync,
    boxStyle = {},
    onOpen = noop,
    onClose = noop,
    searchable,
    searchPlaceholder = "",
    onSearch = noop,
    filter = (inputValue: string, { text, value }: SelectOptionWithGroup) => {
      const optionText = String(typeof text === "string" ? text : value);
      return !searchable || optionText.includes(inputValue);
    },
    autoClearSearchValue = true,
    ...dropdownProps
  } = useDefaultValue(props, null);

  const dropdownRef = useRef<HTMLDivElement>(null);
  const [dropdownWidth, setDropdownWidth] = useState<number>(null);

  const listRef = useRef(null);
  const scrollTo = useListScroll(listRef);
  const [currentIndex, setCurrentIndex] = useState<number>(0);

  useEffect(() => {
    if (dropdownRef.current) {
      setDropdownWidth(dropdownRef.current.clientWidth);
    }
  }, [size, value]);

  const InputRef = useRef<HTMLInputElement>(null);
  const [inputValue, setInputValue] = useState<string>("");
  function focus() {
    if (searchable) {
      setTimeout(() => {
        if (InputRef.current) {
          InputRef.current.focus();
        }
      }, 100); // 第一次展开时 Input 还未渲染
    }
  }

  const selected = value != null && options.find(x => x.value === value);

  // 选项消失不影响已选中值显示
  const [buttonContent, setButtonContent] = useState<React.ReactNode>(() => {
    if (!selected) {
      return undefined;
    }
    return typeof selected.text === "undefined"
      ? selected.value
      : selected.text;
  });

  // options 中有选中项时改变 buttonContent
  useEffect(() => {
    if (selected) {
      setButtonContent(
        typeof selected.text === "undefined" ? selected.value : selected.text
      );
    }
  }, [options]); // eslint-disable-line react-hooks/exhaustive-deps

  // value 变化时改变 buttonContent 及搜索框状态
  useEffect(() => {
    if (selected) {
      setButtonContent(
        typeof selected.text === "undefined" ? selected.value : selected.text
      );
    } else {
      setButtonContent(undefined);
    }
  }, [value]); // eslint-disable-line react-hooks/exhaustive-deps

  // 按钮文字
  const buttonText: React.ReactNode =
    typeof buttonContent !== "undefined"
      ? buttonContent
      : placeholder || t.pleaseSelect;

  const hasGroup = !!options.find(opt => !!opt.groupKey);

  // 筛选
  const filteredOptions = options.filter(options =>
    filter(inputValue, options)
  );
  const len = filteredOptions.length;

  // 同步宽度
  if (boxSizeSync && dropdownWidth) {
    Object.assign(boxStyle, {
      minWidth: dropdownWidth,
      maxWidth: dropdownWidth,
    });
  }

  // 提示
  let { tips } = props;
  if (typeof tips === "undefined" && filteredOptions.length === 0) {
    tips = <EmptyTip />;
  }
  tips = tips ? <List.StatusTip>{tips}</List.StatusTip> : null;

  function handleKeyDown(
    source: "dropdown" | "input",
    event: React.KeyboardEvent,
    // input 托管事件时没有 context，此时一定为 open 状态
    context: DropdownKeyDownContext = { open: true },
    close: () => void = () => null
  ) {
    const option = filteredOptions[currentIndex % len];
    const ok = () => {
      if (option && context.open) {
        // 输入时不能使用空格选中
        if (option.disabled) {
          return false;
        }
        onChange(option.value, { event });
        close();
      }
      return true;
    };
    switch (event.keyCode) {
      case KeyCode.Space:
        if (source === "input") {
          return false;
        }
        event.preventDefault();
        return ok();

      case KeyCode.Enter:
        return ok();

      case KeyCode.Up:
        event.preventDefault();
        setCurrentIndex(c => {
          const index = (c - 1 + len) % len;
          scrollTo(index);
          return index;
        });
        break;

      case KeyCode.Down:
        event.preventDefault();
        setCurrentIndex(c => {
          const index = (c + 1) % len;
          scrollTo(index);
          return index;
        });
        break;

      case KeyCode.Esc:
        close();
        break;
    }
    return true;
  }

  return (
    <Dropdown
      updateOnChildrenChange
      ref={dropdownRef}
      clickClose={false}
      size={size}
      boxStyle={boxStyle}
      button={typeof button !== "undefined" ? button : buttonText}
      onOpen={() => {
        onOpen();
        focus();
      }}
      onClose={() => {
        onClose();
        // 已经未空值不再次置回初始
        if (autoClearSearchValue) {
          setInputValue("");
        }
        if (dropdownRef.current) {
          dropdownRef.current.focus();
        }
      }}
      onKeyDown={(event, context) => handleKeyDown("dropdown", event, context)}
      {...dropdownProps}
    >
      {close => (
        <>
          {searchable && (
            <SearchBox
              simple
              ref={InputRef}
              value={inputValue}
              onChange={value => {
                setInputValue(value);
                onSearch(value);
              }}
              onClear={focus}
              placeholder={searchPlaceholder}
              onKeyDown={event =>
                handleKeyDown("input", event, undefined, close)
              }
            />
          )}
          <List
            ref={listRef}
            type={hasGroup ? "option-group" : "option"}
            onScrollBottom={onScrollBottom}
          >
            {tips}
            {filteredOptions.map((opt, index) => {
              const item = (
                <List.Item
                  key={opt.value}
                  disabled={opt.disabled}
                  selected={opt === selected}
                  current={opt !== selected && currentIndex % len === index}
                  onClick={event => {
                    onChange(opt.value, { event });
                    close();
                  }}
                  tooltip={opt.tooltip}
                >
                  {typeof opt.text === "undefined" ? opt.value : opt.text}
                </List.Item>
              );
              // 包含分组
              if (
                opt.groupKey &&
                (index === 0 ||
                  opt.groupKey !== filteredOptions[index - 1].groupKey)
              ) {
                return (
                  <Fragment key={opt.value}>
                    <List.GroupLabel>{groups[opt.groupKey]}</List.GroupLabel>
                    {item}
                  </Fragment>
                );
              }

              return item;
            })}
            {bottomTips ? <List.StatusTip>{bottomTips}</List.StatusTip> : null}
          </List>
        </>
      )}
    </Dropdown>
  );
}

/**
 * 原生 Select
 */
function NativeSelect(props: SelectProps) {
  const t = useTranslation();
  const { classPrefix } = useConfig();
  const {
    value,
    onChange,
    options = [],
    groups = {},
    placeholder = t.pleaseSelect,
    disabled,
    size,
    className,
    style,
  } = useDefaultValue(props, null);

  return (
    <select
      className={classNames(`${classPrefix}-select`, className, {
        [`size-${
          size === "full" || size === "auto" ? `${size}-width` : size
        }`]: size,
      })}
      style={style}
      disabled={disabled}
      value={value || ""}
      placeholder={placeholder}
      onChange={
        disabled ? null : event => onChange(event.target.value, { event })
      }
    >
      {typeof placeholder === "string" && (
        <option disabled={!!value} hidden>
          {placeholder}
        </option>
      )}
      {options.find(opt => !!opt.groupKey)
        ? groupBy(options).map(group => {
            const { value, groupKey } = group[0];
            if (!groupKey) {
              return group.map(opt => (
                <option
                  key={opt.value}
                  value={opt.value}
                  disabled={opt.disabled}
                  title={
                    typeof opt.tooltip === "string" ? opt.tooltip : undefined
                  }
                >
                  {typeof opt.text === "undefined" ? opt.value : opt.text}
                </option>
              ));
            }
            return (
              <optgroup key={value} label={(groups[groupKey] as string) || ""}>
                {group.map(opt => (
                  <option
                    key={opt.value}
                    value={opt.value}
                    disabled={opt.disabled}
                    title={
                      typeof opt.tooltip === "string" ? opt.tooltip : undefined
                    }
                  >
                    {typeof opt.text === "undefined" ? opt.value : opt.text}
                  </option>
                ))}
              </optgroup>
            );
          })
        : options.map(opt => (
            <option
              key={opt.value}
              value={opt.value}
              disabled={opt.disabled}
              title={typeof opt.tooltip === "string" ? opt.tooltip : undefined}
            >
              {typeof opt.text === "undefined" ? opt.value : opt.text}
            </option>
          ))}
    </select>
  );
}

Select.Multiple = SelectMultiple;
Select.defaultLabelAlign = "middle";
