import React, { useState, Fragment, useRef, useEffect } from "react";
import { Popover, TriggerProps } from "../popover";
import { DropdownBox, CommonDropdownProps } from "../dropdown";
import { StyledProps, Combine } from "../_type";
import { SelectOptionWithGroup } from "../select";
import { List } from "../list";
import { ControlledProps, useDefaultValue } from "../form";
import { Text } from "../text";
import { EmptyTip } from "../tips";
import { useDefault } from "../_util/use-default";
import { KeyCode } from "../_util/key-code";
import { useListScroll } from "../_util/use-list-scroll";

export interface AutoCompleteProps
  extends Combine<CommonDropdownProps, StyledProps, ControlledProps<string>> {
  /**
   * 下拉选项列表
   */
  options?: SelectOptionWithGroup[];

  /**
   * 分组
   */
  groups?: {
    [groupKey: string]: React.ReactNode;
  };

  /**
   * `options` 为空时展示，可使用字符串或 [StatusTip](/component/tips) 相关组件
   */
  tips?: React.ReactNode;

  /**
   * 展示为高亮的关键词
   */
  keyword?: string;

  /**
   * 要包裹的输入组件
   */
  children?: (
    ref: React.Ref<HTMLInputElement | HTMLTextAreaElement>,
    context: { close: () => void }
  ) => React.ReactNode;

  /**
   * `options` 滚动至底部的回调
   */
  onScrollBottom?: (event: React.UIEvent) => void;
}

function highlight(text: React.ReactNode, keyword: string): React.ReactNode {
  if (typeof text !== "string") {
    return text;
  }
  return text.split(keyword).map((text, index) => {
    const key = `${text}-${index}`;
    if (index >= 1) {
      return (
        <Fragment key={key}>
          <strong>{keyword}</strong>
          {text}
        </Fragment>
      );
    }
    return <Text key={key}>{text}</Text>;
  });
}

export function AutoComplete(props: AutoCompleteProps) {
  const {
    className,
    style = {},
    onChange,
    children,
    options = [],
    groups = {},
    tips = "",
    keyword,
    onScrollBottom,
    closeOnScroll = true,
    open,
    defaultOpen,
    onOpenChange,
    ...popoverProps
  } = useDefaultValue(props);
  const [isSelected, setIsSelected] = useState<boolean>(false);
  const inputRef = useRef<HTMLElement>(null);
  const [isOpened, setIsOpened] = useDefault(open, defaultOpen, onOpenChange);
  const [dropdownWidth, setDropdownWidth] = useState<number>(undefined);
  const [currentIndex, setCurrentIndex] = useState<number>(0);
  const listRef = useRef(null);
  const scrollTo = useListScroll(listRef);

  useEffect(() => {
    if (!inputRef.current) return null;
    const len = options.length;
    function handleKeyDown(event: KeyboardEvent) {
      const { value = null, disabled = false } = options[currentIndex] || {};

      switch (event.keyCode) {
        case KeyCode.Up:
          event.preventDefault();
          setCurrentIndex(c => {
            const index = (c - 1 + len) % len;
            scrollTo(index);
            return index;
          });
          break;
        case KeyCode.Down:
          event.preventDefault();
          setCurrentIndex(c => {
            const index = (c + 1) % len;
            scrollTo(index);
            return index;
          });
          break;
        case KeyCode.Enter:
          if (isOpened && options[currentIndex]) {
            if (disabled) break;
            setIsOpened(false);
            onChange(value, { event: event as any });
          }
          break;
        case KeyCode.Esc:
          setIsOpened(false);
          inputRef.current.blur();
          break;
        default:
          setCurrentIndex(0);
          break;
      }
    }
    inputRef.current.addEventListener("keydown", handleKeyDown);
    return () => {
      inputRef.current.removeEventListener("keydown", handleKeyDown);
    };
  }, [
    currentIndex,
    inputRef,
    isOpened,
    onChange,
    options,
    setIsOpened,
    scrollTo,
  ]);

  const hasGroup = !!options.find(opt => !!opt.groupKey);

  return (
    <Popover
      escapeWithReference
      animationScaleFrom={1}
      transitionTimeout={{ enter: 0, exit: 0 }}
      trigger={[AutoCompleteTrigger, { setIsSelected }]}
      placement="bottom-start"
      visible={isOpened && !isSelected}
      onVisibleChange={setIsOpened}
      placementOffset={5}
      closeOnScroll={closeOnScroll}
      {...popoverProps}
      overlay={
        <DropdownBox
          className={className}
          style={{ width: dropdownWidth, ...style }}
        >
          {Array.isArray(options) && options.length > 0 ? (
            <List
              ref={listRef}
              type={hasGroup ? "option-group" : "option"}
              onScrollBottom={onScrollBottom}
            >
              {options.map(
                (
                  { value: optValue, text, disabled, tooltip, groupKey },
                  index
                ) => {
                  const item = (
                    <List.Item
                      key={optValue}
                      current={index === currentIndex}
                      disabled={disabled}
                      tooltip={tooltip}
                      onClick={event => {
                        event.stopPropagation();
                        inputRef.current.focus();
                        onChange(optValue, { event });
                        setIsSelected(true);
                        setCurrentIndex(index);
                      }}
                    >
                      {highlight(text || optValue, keyword)}
                    </List.Item>
                  );

                  if (
                    groupKey &&
                    (index === 0 || groupKey !== options[index - 1].groupKey)
                  ) {
                    return (
                      <Fragment key={optValue}>
                        <List.GroupLabel>{groups[groupKey]}</List.GroupLabel>
                        {item}
                      </Fragment>
                    );
                  }

                  return item;
                }
              )}
            </List>
          ) : (
            !!tips && (
              <List type="option">
                <List.StatusTip>
                  {typeof tips === "string" ? (
                    <EmptyTip emptyText={tips} />
                  ) : (
                    tips
                  )}
                </List.StatusTip>
              </List>
            )
          )}
        </DropdownBox>
      }
    >
      {children(
        dom => {
          inputRef.current = dom;
          if (dom) {
            setDropdownWidth(dom.offsetWidth);
          }
        },
        { close: () => setIsOpened(false) }
      )}
    </Popover>
  );
}

export function AutoCompleteTrigger({
  visible,
  setVisible,
  openDelay = 0,
  closeDelay = 0,
  render,
  setIsSelected,
}: TriggerProps & { setIsSelected: (s: boolean) => void }) {
  const commonProps = {
    onFocus: () => setVisible(true, openDelay),
    onChange: () => {
      setIsSelected(false);
      if (!visible) {
        setVisible(true, openDelay);
      }
    },
    onClick: () => {
      setIsSelected(false);
      if (!visible) {
        setVisible(true, openDelay);
      }
    },
    onBlur: () => setVisible(false, closeDelay),
  };
  return render({
    overlayProps: { ...commonProps, tabIndex: 1000 },
    childrenProps: commonProps,
  });
}

AutoComplete.defaultLabelAlign = "middle";
